*/
html, body, div, span,
h1, h2, h3, h4, h5, h6, p, blockquote,
a, img, b, u, i,
ol, ul, li,
fieldset, form, label, legend,
article, aside,
figure, figcaption, footer, header, hgroup,
menu, nav, section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
}

h2 {
  font-size: 6.5rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.2rem;
}

h5,
h6 {
  font-size: 1.4rem;
}

h5 {
  font-weight: lighter;
}

h6 {
  font-weight: bolder;
}

p {
  font-size: 1.4rem;
}

a {
  text-decoration: none;
}

@media screen and (max-width: 1000px) {
  h2 {
    font-size: 4rem;
  }
  h4 {
    font-size: 1.8rem;
  }
}

