.header {
  width: 100%;
  background-image: url("../images/banner.jpg");
  background-size: cover;
  min-height: 750px;
}

.header__top-container {
  width: 100%;
  overflow: hidden;
  position: fixed;
}

.header__top, .fixed {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  max-width: 1170px;
  width: 100%;
  padding: 40px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  transition: padding 0.3s;
}

.header__bottom {
  text-align: center;
  color: #fff;
  max-width: 850px;
  margin: 0 auto;
  padding-top: 106px;
}

.header__bottom__title {
  padding-top: 150px;
  padding-bottom: 50px;
}

.header__bottom__slogan {
  padding-bottom: 80px;
}

.active-box {
  line-height: 0;
  text-indent: -10000px;
}

.fixed {
  padding: 20px 0;
  border-bottom: 0px;
  transition: padding 0.3s;
}

.fixed-background {
  background-color: #292c47;
  z-index: 150;
}

.header.menu-opened .header__top, .header.menu-opened .fixed {
  height: 400px;
  background-color: #2b2c48;
}

@media screen and (max-width: 1000px) {
  .header {
    min-height: 630px;
  }
  .header__bottom {
    padding-top: 70px;
  }
}

@media screen and (max-width: 860px) {
  .header__bottom__title {
    width: 80%;
    margin: 0 auto;
  }
  .header__bottom__slogan {
    width: 80%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 500px) {
  .header__bottom {
    padding-top: 0px;
  }
  .header__bottom__slogan {
    line-height: 22px;
  }
}

@media screen and (max-width: 400px) {
  .header__bottom__slogan {
    padding-bottom: 60px;
  }
}

@media screen and (max-width: 325px) {
  .header {
    min-height: 670px;
  }
}

.features, .our-team {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  padding: 140px 0;
}

.feature {
  text-align: center;
  width: 33.33%;
}

.feature--top-row {
  padding-bottom: 70px;
}

.feature__image {
  padding-bottom: 35px;
}

.feature__title {
  padding-bottom: 25px;
}

.feature__description {
  max-width: 300px;
  margin: 0 auto;
  line-height: 22px;
}

@media screen and (max-width: 1000px) {
  .feature__description {
    max-width: 250px;
  }
}

@media screen and (max-width: 800px) {
  .feature {
    width: 50%;
  }
}

@media screen and (max-width: 550px) {
  .features, .our-team {
    padding: 100px 0;
  }
  .feature {
    width: 100%;
    padding-bottom: 50px;
  }
  .feature:last-child {
    padding-bottom: 0px;
  }
}

.works {
  max-width: 2000px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}

.work {
  width: 25%;
  height: 350px;
  overflow: hidden;
  position: relative;
  opacity: 0;
  box-sizing: border-box;
}

.work:hover .work__background {
  visibility: visible;
  opacity: 0.8;
}

.work:hover .work__image {
  transform: scale(1.2);
}

.work:hover .work__description__top, .work:hover .work__description__bottom {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.work:nth-child(2), .work:nth-child(6) {
  width: 25.1%;
}

.work:nth-child(3), .work:nth-child(7) {
  width: 24.9%;
}

.work__image {
  min-height: 100%;
  align-self: stretch;
  display: block;
  transition: all 0.3s;
}

.work__background {
  overflow: hidden;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background-color: #e14d4d;
