@keyframes work-flip {
  0% {
    opacity: 0;
    transform: translateX(-500px);
  }
  50% {
    transform: translateX(0px);
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes work-flip2 {
  0% {
    opacity: 0;
    transform: translateX(500px);
  }
  50% {
    transform: translateX(0px);
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes button {
  0% {
    transform: rotate(0deg) scale(1);
  }
  30% {
    transform: rotate(-5deg) scale(1.15);
  }
  60% {
    transform: rotate(5deg) scale(1.15);
  }
  100% {
    transform: rotate(0deg) scale(1);
  }
}
